
#footer {
	height: 64px;
	width: 100%;
	margin-top: 10px;
	position: fixed;
	left:0;
	bottom:0;
}
#footer ul {
	width:100%;
	height: 100%;
	border-top:1px solid #d9d9d9;
	background-color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 1.4rem;
}
#footer ul li {
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	text-align: center;
}
#footer ul li a {
	height: 100%;
	padding: 4px 10px 2px;
	display: block;
	color:#3b68b8;
	background: red;
	line-height: 90px;
	background: url(../assets/img/footer.png) no-repeat;
	background-position: center 10px;
	background-size: 40px 571px;
}
#footer ul .foo-icon-1.active a {
	background-position: center -57px;
}
#footer ul .foo-icon-2 a {
	background-position: center -140px;
}
#footer ul .foo-icon-2.active a {
	background-position: center -216px;
}
#footer ul .foo-icon-3 a {
	background-position: center -291px;
}
#footer ul .foo-icon-3.active a {
	background-position: center -359px;
}
#footer ul .foo-icon-4 a {
	background-position: center -445px;
}
#footer ul .foo-icon-4.active a {
	background-position: center -518px;
}



