
#toolManage {
	z-index: 10;
}
#SelectedTool {
	border-bottom: 10px solid #ECF1F5;
}
.toolTitle {
	border-top: 1px solid #ECF1F5;
	font-size: 18px;
	color: #3866BB;
	text-align: left;
	padding: 5px 8px;
}
.tool_M_Li {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	border-top: 1px solid #ECF1F5;
	padding: 6px 10px;
}
.tool_M_Li .toolM {
	width: 20px;
	height: 50px;
	background: url(../../assets/img/sub-btn.png) no-repeat;
	background-size: cover;
	background-position: center 11px;
	margin-right: 10px;
	vertical-align: middle;
}
.tool_M_Li .toolM_add {
	background-position: center -23px;
}
.tool_M_Li .toolM_btn {
	background-position: center -58px;
}
.tool_M_Li .toolImg {
	width: 50px;
	height: 50px;
	vertical-align: middle;
}
.tool_M_Li .toolImg img {
	width: 100%;
	height: 100%;
}
.tool_M_Li .toolM_txt {
	text-align: left;
	margin-left: 5px;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
.tool_M_Li .toolM_txt h2 {
	font-size: 16px;
	color: #4a4a4a;
}
.tool_M_Li .toolM_txt span {
	color: #9c9c9c;
	font-size: 12px;
	line-height: 10px;
}



