
#mysub {
	border-bottom: 10px solid #ECF1F5;
}
.subTitle {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 10px;
	border-top: 1px solid #ECF1F5;
	padding-right: 18px;
}
.subTitle h2 {
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	text-align: left;
	font-size: 16px;
}
.subTitle span {
	font-size: 14px;
	line-height: 24px;
}
.SubList {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	border-top: 1px solid #ECF1F5;
	padding: 12px 16px;
}
.subList {
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	line-height: 16px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.subList img {
	display: block;
	width: 50px;
	height: 50px;
	margin-right: 12px;
}
.subList .subList_main {
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	color: #9B9B9B;
	text-align: left;
}
.subList .subList_main .subList_top {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin: 4px 0 12px;
}
.subList .subList_main .subList_top h3 {
	font-size: 16px;
	margin-right: 8px;
	color: #4A4A4A;
}
.subList .subBtn ,.subBtn{
	width: 30px;
	height: 50px;
	background: url(../assets/img/sub-btn.png) no-repeat;
	background-size: cover;
	/*z-index: 1;*/
}
.subList .subBtn_1 ,.subBtn_1{
	background-position: center 9px;
}
.subList .subBtn_2 ,.subBtn_2{
	background-position: center -97px;
}
.subList .subBtn_3 ,.subBtn_3{
	background-position: center -47px;
}



