	margin-bottom: 64px;
}*/
.I-M-li {
	padding: 5px 8px;
	border-bottom: 1px solid #ecf1f5;
}
.news {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.news .img {
	width: 132px;
	height: 80px;
	margin-right: 4vw;
}
.news .img img{
	width: 100%;
	height: 100%;
}
.news .describe {
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	text-align: left;
}
.news .describe h2 {
	font-size: 14px;
	height: 58px;
	color: #4a4a4a;
}
.news .describe .tips {
	height: 16px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	color: #9b9b9b;
}
.news .describe .tips .lei {
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
.news .describe .tips .IM_num {
	padding-left: 20px;
	height: 16px;
	background: url(../assets/img/show-comment.png) no-repeat;
	background-position: 0 0px;
	background-size: contain;
}



