
.shop_li {
	border-top: 10px solid #ECF1F5;
	background-color: #fff;
}
.shop_li .shop_title {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 10px 5px 0;
	color: #4a4a4a;
	border-bottom: 1px solid #ecf1f5;
}
.shop_li .shop_title h2 {
	display: inline;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	text-align: left;
	font-size: 16px;
	vertical-align: middle;
}
.shop_li .shop_title a {
	color: #4a4a4a;
	line-height: 24px;
	vertical-align: middle;
}
.shop_li .shop_main {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.shop_li .shop_main a {
	display: block;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	padding: 4px;
	border-left: 1px solid #ECF1F5;
	text-align: left;
}
.shop_li .shop_main a img {
	border-radius: 8px;
	height: 80px;
	/*width: 100px;*/
	margin-bottom: 4px;
}
.shop_li .shop_main a span {
	font-size: 14px;
	color: #4a4a4a;
}



