
.tool_Li {
	border-top: 1px solid #ECF1F5;
}
.tool_Li .tool_Li_title {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 6px 10px;
	border-bottom: 1px solid #ECF1F5;
	color: #3866bb;
}
.tool_Li .tool_Li_title .game_icon img {
	width: 26px;
	height: 26px;
	border-radius: 20px;
}
.tool_Li .tool_Li_title h2 {
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	text-align: left;
	font-size: 18px;
	padding-left: 10px;
}
.tool_Li .tool_Li_title span {
	font-size: 16px;
	line-height: 26px;
}
.tool_Li .tool_Li_main {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	margin-bottom: 16px;
}
.tool_Li .tool_Li_main li {
	-ms-flex-preferred-size: 33%;
	    flex-basis: 33%;
}
.tool_Li .tool_Li_main li a {
	display: block;
}
.tool_Li .tool_Li_main li a img {
	width: 80px;
	margin: 0 auto;
}



