
#hunt {
	width: 100%;
	height: 100%;
	background-color: #fff;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;
}
#hunt-header {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 90vw;
	margin: 10px auto;
}
#hunt-header input {
	background: url(../assets/img/header.png) no-repeat;
	background-position: 0 -146px;
	background-size: 30px;
	padding-left: 34px;
	height: 32px;
	border: none;
	background-color: #ecf1f5 ;
	border-radius: 10px;
	margin-right: 10px;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	color: #3b68b8;
}
#hunt-header input::-webkit-input-placeholder {
	color: #3b68b8;
}
#hunt-header a {
	width: 32px;
	height: 32px;
	display: block;
	background: url(../assets/img/escBtn.png) no-repeat;
	background-size: cover;
	margin-right: -10px;
}
#hunt-main {
}
#hunt-main .hun-B {
	border-bottom: 1px solid #ecf1f5;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
#hunt-main .hun-B .hun-btn {
	display: inline-block;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	color: #9b9b9b;
	padding-bottom: 5px;
}
#hunt-main .hun-B .active {
	color: #3b68b8;
}
#hunt-main .hun-M .hun-sou1 {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	/*display: none;*/
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	padding: 4px 5vw;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}
#hunt-main .hun-M .hun-sou1 li {
	-ms-flex-preferred-size: 20%;
	    flex-basis: 20%;
	width: 80px;
	height: 36px;
	margin: 2px 2px;
	line-height: 36px;
	background-color: #ecf1f5;
	border-radius: 8px;
}
#hunt-main .hun-M .hun-sou2 {
	/*display: flex;*/
	/*flex-wrap: wrap;*/
	padding: 5px 0;
}
#hunt-main .hun-M .hun-sou2 li {
	/*flex: 1;*/
	text-align: left;
	padding-left: 18px;
	border-bottom: 1px solid #ECF1F5;
	height: 28px;
	line-height: 28px;
}
#hunt-main .hun-M .hun-sou2 li span {
	content: "";
	display: block;
	height: 28px;
	width: 28px;
	float: right;
	margin-right: 15px;
	background: url(../assets/img/escBtn.png) no-repeat;
	background-size: 18px 18px;
	background-position: center;
}
#hunt-main .hun-M 	 .hun-delete {
	display: inline-block;
	padding: 8px 12px;
	background-color: #ecf1f5;
	border-radius: 8px;
}



