
.forum_Li .forum_li {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background: #FFFFFF;
	border-top: 1px solid #ECF1F5;
	padding: 12px 30px;
}
.forum_Li .forum_li .game_icon {
	width: 56px;
	height: 56px;
}
.forum_Li .forum_li .game_icon img {
	width: 100%;
	height: 100%;
}
.forum_Li .forum_li .forum_txt {
	margin-left: 30px;
	text-align: left;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
.forum_Li .forum_li .forum_txt h2 {
	font-size: 20px;
	color: #000;
	margin-bottom: 8px;
}
.forum_Li .forum_li .forum_txt span {
	color: #666666;
}
.forum_Li .forum_li .forum_functionIcon {
	width: 34px;
	height: 56px;
	background: url(../assets/img/sub-btn.png) no-repeat;
	background-size: cover;
	background-position: center -109px;
}



